@import "https://cdn.jsdelivr.net/npm/reset-css@5.0.1/reset.min.css";
@import url("https://fonts.googleapis.com/css2?family=Nanum+Myeongjo:wght@400;700;800&family=Song+Myung&display=swap");

* {
  box-sizing: border-box;
}

input {
  all: unset;
  box-sizing: border-box;
  appearance: none;
}

html {
  font-size: 10px;
}

body {
  color: #2d3436;
  font-size: 1.6rem;
  background-color: #ffeaa7;
  margin: 3rem;
  font-family: "Song Myung", serif;
}

form {
  width: 100%;
}

.home {
  display: flex;
  flex-direction: column;
}

.title__container {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 1rem 0px 3rem 0px;
}
.home__title {
  font-size: 6rem;
  font-family: "Song Myung", serif;
  font-weight: 400;
}

.video__container {
  display: flex;
  justify-content: center;
  align-items: center;
}

.video__player video:focus {
  outline: none;
}

.greeting__container {
  height: 30vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.greeting__description-container {
  width: 70%;
}

.greeting__title {
  margin: 1rem 0px 4rem 0px;
  font-size: 3.2rem;
  font-weight: 700;
}

.greeting__description {
  width: 100%;
  font-size: 1.8rem;
  line-height: 1.5;
}

.greeting__description-date {
  display: flex;
  justify-content: flex-end;
  margin-top: 2rem;
}

.poem {
  padding: 4rem;
}

.poem__title {
  font-size: 2.8rem;
  font-weight: 700;
  margin: 3rem 0rem;
}

.poem__container {
  width: 100%;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(10rem, 25rem));
  column-gap: 2rem;
  margin-bottom: 7rem;
}

.poem__card {
  display: flex;
  flex-direction: column;
  cursor: pointer;
  margin-bottom: 1rem;
}

.poem__photo {
  width: 25rem;
  height: 25rem;
  background-size: cover;
  border-radius: 3%;
  background-position: center center;
}

